'use client';
import { useState, useEffect, useCallback } from 'react';
import { supabase } from '@/lib/supabase';
import { AGENTS } from '@/lib/agents';
import OfficeCanvas from '@/components/OfficeCanvas';
import AgentPanel from '@/components/AgentPanel';
import EventFeed from '@/components/EventFeed';
import ChatLog from '@/components/ChatLog';
import MissionBoard from '@/components/MissionBoard';
import StatsBar from '@/components/StatsBar';

export default function Home() {
  const [agents, setAgents] = useState([]);
  const [events, setEvents] = useState([]);
  const [messages, setMessages] = useState([]);


  const fetchAll = useCallback(async () => {
    // Drill Mode: Mock Data
    const mockAgents = Object.keys(AGENTS).map(name => ({
       name,
       ...AGENTS[name],
       status: 'idle',
       current_task: 'Ready for drill',
       current_room: 'desk'
    }));
    setAgents(mockAgents);
    setEvents([{ id: 1, title: 'Drill Started', description: 'System check initiated', type: 'system', created_at: new Date().toISOString() }]);
    setMessages([{ id: 1, author: 'System', content: 'Drill initiated.', created_at: new Date().toISOString() }]);
  }, []);

  useEffect(() => {
    fetchAll();

    // Drill Sequence
    const timer1 = setTimeout(() => {
        setAgents(prev => prev.map(a => ({ ...a, status: 'working', current_task: 'Drill: Working (Animation Check)' })));
    }, 2000);
    
    const timer2 = setTimeout(() => {
        setAgents(prev => prev.map(a => ({ ...a, status: 'idle', current_task: 'Drill: Idle (Complete)' })));
    }, 12000); // 2s + 10s

    return () => {
        clearTimeout(timer1);
        clearTimeout(timer2);
    };
  }, [fetchAll]);



  return (
    <div className="hq-page">
      <StatsBar agents={agents} events={events} />

      <div className="hq-main">
        <div className="hq-sidebar-left">
          <AgentPanel agents={agents} />
        </div>
        <div className="hq-center">
          <OfficeCanvas agents={agents} />
        </div>
        <div className="hq-sidebar-right">
          <MissionBoard />
        </div>
      </div>

      <div className="hq-bottom">
        <EventFeed events={events} />
        <ChatLog messages={messages} />
      </div>
    </div>
  );
}
